import hello from '-!svg-react-loader!assets/images/undraw_hello_aeia.svg';
import { Col, Form, Row } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { Alert } from 'components/Alert';
import { Checkbox, Email, Password, Submit, Text } from 'components/Form';
import * as AlertCtx from 'context/Alert';
import * as Auth from 'context/Auth';
import * as React from 'react';
import { compose } from 'react-apollo';
import { FormattedMessage, InjectedIntlProps, injectIntl } from 'react-intl';
import { Wrapper } from './';
const Hello: any = hello;

interface IProps
  extends FormComponentProps,
    InjectedIntlProps,
    Auth.InjectedProps,
    AlertCtx.InjectedProps {}

interface IState {
  passwordValue: string | null;
}

class SignupForm extends React.PureComponent<IProps, IState> {
  state = {
    passwordValue: null,
  };

  passwordRef: HTMLInputElement | null = null;
  handleChangePassword: ((node: React.ChangeEvent) => void) | undefined;
  handleSetPasswordRef: ((node: HTMLInputElement) => void) | undefined;
  handleSubmit: (e: React.FormEvent) => void;

  constructor(props: any) {
    super(props);

    this.handleChangePassword = this.changePassword.bind(this);
    this.handleSetPasswordRef = this.setPasswordRef.bind(this);
    this.handleSubmit = this.submit.bind(this);
  }

  submit = async (e: React.FormEvent) => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        delete values.offers;
        values.passwordConfirmation = values['validate-password'];
        delete values['validate-password'];
        await (this.props.auth &&
          this.props.auth.signup &&
          this.props.auth.signup(values));
      }
    });
  };

  setPasswordRef(node: HTMLInputElement) {
    this.passwordRef = node;
  }

  changePassword() {
    this.setState({
      passwordValue: this.passwordRef && this.passwordRef.value,
    });
  }

  componentWillUnmount() {
    const { alert } = this.props;
    alert && alert.reset && alert.reset();
  }

  escapeRegExp(text?: string | null): string {
    return text ? text.replace(/[-[\]{}()*+?.,\\^$|#\s]/g, '\\$&') : '';
  }

  render() {
    const { form, intl, alert } = this.props;
    const errors = alert && alert.errors;
    const { passwordValue } = this.state;

    return (
      <Wrapper img={<Hello />} className="signup-wrapper" title="signup.title">
        {errors &&
          errors.map((error, i) => (
            <Alert
              key={`${i}`}
              alertItem={error}
              message={error.message && <FormattedMessage id={error.message} />}
              type="error"
            />
          ))}
        <Form className="form-signup" onSubmit={this.handleSubmit}>
          <Row gutter={28}>
            <Col className="gutter-box" span={12}>
              <Text
                validateTrigger="onSubmit"
                form={form}
                label={<FormattedMessage id="signup.form.firstname" />}
                id="firstname"
                rules={[
                  {
                    message: intl.formatMessage({
                      id: 'signup.form.firstname_error',
                    }),
                    required: true,
                  },
                ]}
              />
            </Col>
            <Col span={12}>
              <Text
                validateTrigger="onSubmit"
                form={form}
                label={<FormattedMessage id="signup.form.lastname" />}
                id="lastname"
                rules={[
                  {
                    message: intl.formatMessage({
                      id: 'signup.form.lastname_error',
                    }),
                    required: true,
                  },
                ]}
              />
            </Col>
          </Row>
          <Email
            validateTrigger="onSubmit"
            label={<FormattedMessage id="signup.form.email" />}
            id="email"
            rules={[
              {
                message: intl.formatMessage({
                  id: 'signup.form.email_error',
                }),
                pattern: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
                required: true,
              },
            ]}
            form={form}
          />
          <Password
            validateTrigger="onSubmit"
            label={<FormattedMessage id="signup.form.password" />}
            rules={[
              {
                message: `${intl.formatMessage({
                  id: 'signup.form.password_error',
                })}\n`,
                required: true,
              },
              {
                message: intl.formatMessage({
                  id: 'signup.form.password_strong_error',
                }),
                pattern: /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[\!\“\#\$\%\&\‘\(\)\*\+\,\-\.\/\:\;\<\=\>\?\@\[\\\]\^\_\`\{\|\}\~])(?=.{8,})/,
                required: true,
              },
            ]}
            inputRef={this.handleSetPasswordRef}
            onChange={this.handleChangePassword}
            form={form}
            hint={intl.formatMessage({
              id: 'signup.form.password_hint',
            })}
          />
          <Password
            validateTrigger="onSubmit"
            label={<FormattedMessage id="signup.form.comfirm_password" />}
            form={form}
            rules={[
              {
                message: intl.formatMessage({
                  id: 'signup.form.comfirm_password_error',
                }),
                pattern: new RegExp(`^${this.escapeRegExp(passwordValue)}$`),
                required: true,
              },
            ]}
            id="validate-password"
          />
          <Checkbox
            validateTrigger="onSubmit"
            id="cgu"
            label={
              <span>
                <FormattedMessage id="signup.form.legal" />{' '}
                <a target="_blank" href="/cgu.pdf">
                  <u className="main-color">
                    <FormattedMessage id="signup.form.legal_link" />
                  </u>
                </a>
              </span>
            }
            rules={[
              {
                message: intl.formatMessage({
                  id: 'signup.form.legal_error',
                }),
                required: true,
              },
            ]}
            form={form}
          />
          <Checkbox
            validateTrigger="onSubmit"
            id="offers"
            label={<FormattedMessage id="signup.form.partners" />}
            form={form}
          />
          <Submit label={<FormattedMessage id="signup.form.submit" />} />
        </Form>
      </Wrapper>
    );
  }
}

export default compose(
  Form.create({}),
  injectIntl,
  Auth.hoc(),
  AlertCtx.hoc(),
)(SignupForm);
